<template>
    <el-dialog  title='域名导入' :visible.sync="dialogForm" class="updatePwd">

        <el-form :model="form">
            <el-form-item label="用途:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model.trim="form.purpose" >
                    <el-option
                            v-for="(item, $key) in purpose"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="url" :label-width="formLabelWidth">
                <el-input
                        v-model.trim="form.url"
                        type="textarea"
                        :rows="2"
                        placeholder="请输入域名, 用','连接">
                </el-input>
                <div @click="formatUrlsStr()" class="geshi">格式化</div>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">取 消</el-button>
            <el-button type="primary" @click="submit()">提交</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .geshi{
        border:1px solid #ddd;
        border-radius:4px;
        height:40px;
        line-height:40px;
        text-align:center;
        margin-top:20px;
        cursor:pointer;
    }
    .updatePwd{
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:30px 50px 0 0;
        }
    }
</style>

<script>
    import QuRequest from '../../core/QuRequest'

    const quRequest = new QuRequest()

    export default {
        data() {
            return {

                purpose:[
                    { 'name': '默认', 'value': '0'},
                    { 'name': '微信','value': '1'},
                    { 'name': '朋友圈', 'value': '2'},
                    { 'name': '二维码', 'value': '3'},
                    { 'name': 'QQ','value': '4'},
                    { 'name': '专用', 'value': '5'},
                    { 'name': '微博', 'value': '6'},
                    { 'name': '视频教程', 'value': '7'},
                    { 'name': '唤醒徒弟','value': '8'},
                    { 'name': '面对面收徒', 'value': '9'},
                ],
                dialogForm:false,
                formLabelWidth: '200px',
                radioShow:false,
                disabled:true,
                isShow:true,
                form:{
                    url: '',
                    purpose:'0',
                }
            }
        },
        methods: {

            showInfoDialog() {
                this.dialogForm = true;
            },
            formatUrlsStr: function() {
                this.form.url = this.form.url.replace(/(.+)(?:\n)|(?:\r\n)/g, '$1,');
            },
            submit(){
                quRequest.send((quRequest.Apis.qutuiba.domain.domainImport),this.form
                ).then(res => {
                    this.$message.success("提交成功！");
                    this.$emit('update')
                }).catch(err => {
                    this.$message.error(`获取内容列表数据失败：${ err.message || '未知错误' }`);
                }).fin(() => {
                    this.loading = false;
                }).done();
                this.dialogForm = false;
            }
        }
    }
</script>


